import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Button, Menu, MenuItem } from '@mui/material';
import './dropdown.css';

const DropdownButton = () => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
        Menu
      </Button>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
       <MenuItem onClick={handleClose} ><Link to="/"><p className="green">Bienvenidos </p></Link></MenuItem>
        <MenuItem onClick={handleClose} ><Link to="/Secondpage"><p className="green">Nuevo Proyecto</p></Link></MenuItem>
        <MenuItem onClick={handleClose} ><Link to="/Contact"><p className="green">Contacto</p></Link></MenuItem>
        <MenuItem onClick={handleClose} ><Link to="/donaciones"><p className="green">Donaciones</p></Link></MenuItem>
      </Menu>
      
    </>
  );
};

export default DropdownButton;
